.backgroundVideo {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;

}

.main-container {
  
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
  object-fit: contain;
}


.main-container > h1 {
  color: #fff;
  margin-top: -100px;
  font-size: 85px;
  font-family: "Inter", sans-serif;
}

.main-container > p{
  margin-top: 8px;
  color: #fff;
  
}

@media screen and (max-width: 960px){
  .main-container > h1{
      font-size: 50px;
      margin-top: 150px;
  }

}

@media screen and (max-width: 760px){
  .main-container > h1{
      font-size: 50px;
      margin-top: -100px;
  }

  .main-container > p{
      font-size: 30px;
  }

}